<template>
	<view :class="{body:models=='1'||models=='', bodyNight:models=='0'}">
		<!-- 弹窗 -->
		<view class="model" v-if="showAudition">
			<view class="models">
				<img src="../../static/index/modelBg.png">
				<view class="closeModel" @click="closeAudition">×</view>
				<view class="contents">
					<view class="modelTitle">请输入邀请码</view>
					<input type="text" v-model="auditionCode" />
					<view class="buttons">
						<button class="sure" @click="sendAuditionCode">确定</button>
						<button class="contact" open-type="contact">联系客服</button>
					</view>
				</view>
			</view>
		</view>
		<view class="head">
			<view class="list_cell" @click="oneTab(item,index)" v-for="(item,index) in data" :key="index">
				<view :class="{cells:true, purple:index==0, orange:index==1, blue:index==2, green:index==3}">
					<view class="title">{{item.title}}</view>
					<view class="num">
						{{item.three_leve_question_count || item.three_leve_wrong_count || item.three_leve_collection_count}}题
					</view>
				</view>
				<view v-show="current==index" class="icon"></view>
			</view>
		</view>
		<view class="list">
			<view class="cell" v-for="(item,index) in dataList" :key="index">
				<view class="oneList" @click="open(index)">
					<view class="oneLeft">
						<view class="title">{{item.title}}</view>
						<view class="icon" v-if="item.attr">{{item.attr}}</view>
					</view>
					<view class="oneRight">
						<view class="number">
							<view>
								<view :class="{text:item.two_leve_do_question_count>=1}">{{item.two_leve_do_question_count}}</view>
								<view :class="{text:item.two_leve_wrong_count>=1}">{{item.two_leve_wrong_count}}</view>
								<view :class="{text:item.two_leve_collection_count>=1}">{{item.two_leve_collection_count}}</view>
							</view>
							<view v-if="item.two_leve_question_count">/{{item.two_leve_question_count}}</view>
						</view>
						<img src="/static/index/right_icon.png"
							:class="{arrow:true, arrow_checked:item.checked==true}"></img>
					</view>
				</view>
				<view :class="{twoList:true, display_none:item.checked==false}"
					v-for="(value,key) in dataDeatils[index]" :key="key" @click="toDetails(value)">
					<view class="twoListLeft">
						<view :class="{point:true, point_blue:value.one_leve_do_question_count>0}"></view>
						<view class="title">{{value.title}}</view>
						<view class="icon" v-if="value.attr">{{value.attr}}</view>
					</view>
					<view class="twoListRight">
						<view class="number">
							<view>
								<view :class="{text:value.one_leve_do_question_count>=1}">{{value.one_leve_do_question_count}}</view>
								<view :class="{text:value.one_leve_wrong_count>=1}">{{value.one_leve_wrong_count}}</view>
								<view :class="{text:value.one_leve_collection_count>=1}">{{value.one_leve_collection_count}}</view>
							</view>
							<view v-if="value.one_leve_question_count">/{{value.one_leve_question_count}}</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		<!-- 加载动画 -->
		<view class="loadModul" v-if="show">
			<u-loading-icon></u-loading-icon>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		siteBrushThreeMenu,
		siteBrushTwoMenu,
		siteBrushOneMenu,
		siteBrushTwoMenuCollection,
		siteBrushOneMenuCollection,
		siteBrushTwoMenuWrong,
		siteBrushOneMenuWrong,
		siteBrushWrong,
		siteBrushCollection,
		question,
		invitation
	} from '@/common/http.api.js';
	export default {
		data() {
			return {
				show: false, //加载动画
				showAudition: false, //弹窗显示状态
				auditionCode: '', //邀请码
				current: 0, //当前选中
				models: '1', //白天模式
				wrong: '',
				enter: '',
				currents: '',
				data: {}, // 一级列表
				dataList: {}, // 二级列表
				dataDeatils: [], // 三级列表
			}
		},
		onLoad(option) {
			this.show = true
			this.time = new Date().getFullYear()
			console.log(this.time)
			this.enter = option.enter
			this.currents = option.currents
			this.models = store.state.model
			this.token = store.state.vuex_token
			uni.getStorage({
				key: 'last_time',
				success: (res)=> {
					this.current = res.data
					console.log('上次:',res.data);
				}
			});
			if (this.models == '0') {
				console.log('进入夜间模式')
				uni.setNavigationBarColor({
					frontColor: '#ffffff',
					backgroundColor: '#303030',
					animation: {
						duration: 0,
						timingFunc: 'easeIn'
					}
				})
			} else {
				console.log('进入白天模式')
				uni.setNavigationBarColor({
					frontColor: '#ffffff',
					backgroundColor: '#6ab4ff',
					animation: {
						duration: 0,
						timingFunc: 'easeIn'
					}
				})
			}

			if (option.enter == 0) {
				// 首页
				if(this.token){
					siteBrushThreeMenu({
						token: this.token
					}).then(res => {
						console.log(res)
						this.data = res.data.data
						siteBrushTwoMenu({
							token: this.token,
							id: res.data.data[this.current].id
						}).then(res => {
							console.log(res)
							this.dataList = res.data.data
							for (let i in this.dataList) {
								this.dataList[i].checked = false
							}
							for (let i in res.data.data) {
								this.dataDeatils[i] = new Array()
							}
							this.show = false
							console.log(this.dataDeatils)
						}).catch(err => {
							console.log(err)
						})
					}).catch(err => {
						console.log(err)
					})
				}else{
					siteBrushThreeMenu().then(res => {
						console.log(res)
						this.data = res.data.data
						siteBrushTwoMenu({
							id: res.data.data[this.current].id
						}).then(res => {
							console.log(res)
							this.dataList = res.data.data
							for (let i in this.dataList) {
								this.dataList[i].checked = false
							}
							for (let i in res.data.data) {
								this.dataDeatils[i] = new Array()
							}
							this.show = false
							console.log(this.dataDeatils)
						}).catch(err => {
							console.log(err)
						})
					}).catch(err => {
						console.log(err)
					})
				}
				
			} else {
				console.log("复习入口")
				// 复习
				if (option.currents == 0) {
					//错题
					this.wrong = 1
					uni.setNavigationBarTitle({
						title: "考点刷题错题集"
					});
					siteBrushWrong({
						token: this.token
					}).then(res => {
						console.log(res)
						this.data = res.data.data
						siteBrushTwoMenuWrong({
							token: this.token,
							id: res.data.data[this.current].id
						}).then(res => {
							console.log(res)
							this.dataList = res.data.data
							for (let i in this.dataList) {
								this.dataList[i].checked = false
							}
							for (let i in res.data.data) {
								this.dataDeatils[i] = new Array()
							}
							this.show = false
							console.log(this.dataDeatils)
						}).catch(err => {
							console.log(err)
						})
					}).catch(err => {
						console.log(err)
					})
				} else {
					//收藏
					uni.setNavigationBarTitle({
						title: "考点刷题收藏集"
					});
					siteBrushCollection({
						token: this.token
					}).then(res => {
						console.log(res)
						this.data = res.data.data
						siteBrushTwoMenuCollection({
							token: this.token,
							id: res.data.data[this.current].id
						}).then(res => {
							console.log(res)
							this.dataList = res.data.data
							for (let i in res.data.data) {
								this.dataDeatils[i] = new Array()
							}
							this.show = false
							console.log(this.dataDeatils)
						}).catch(err => {
							console.log(err)
						})
					}).catch(err => {
						console.log(err)
					})
				}
			}
		},
		onShow() {
			if (this.currents == 0) {
				//错题
				siteBrushWrong({
					token: this.token
				}).then(res => {
					console.log(res)
					this.data = res.data.data
					siteBrushTwoMenuWrong({
						token: this.token,
						id: res.data.data[this.current].id
					}).then(res => {
						console.log(res)
						this.dataList = res.data.data
						for (let i in this.dataList) {
							this.dataList[i].checked = false
						}
						for (let i in res.data.data) {
							this.dataDeatils[i] = new Array()
						}
						this.show = false
						console.log(this.dataDeatils)
					}).catch(err => {
						console.log(err)
					})
				}).catch(err => {
					console.log(err)
				})
			}
		},
		methods: {
			open(e) {
				this.show = true
				for (let i in this.dataList) {
					if (i == e) {
						this.dataList[e].checked = !this.dataList[e].checked
						console.log('checked e', this.dataList[e].checked)
					} else {
						this.dataList[i].checked = false
						console.log('checked i', this.dataList[i].checked)
					}
				}
				if (this.enter == 0) {
					// 首页
					if(this.token){
						siteBrushOneMenu({
							token: this.token,
							id: this.dataList[e].id
						}).then(res => {
							console.log(res)
							this.$set(this.dataDeatils, e, res.data.data)
							this.show = false
							console.log(this.dataDeatils)
							console.log(this.dataDeatils[e])
						}).catch(err => {
							console.log(err)
						})
					}else{
						siteBrushOneMenu({
							id: this.dataList[e].id
						}).then(res => {
							console.log(res)
							this.$set(this.dataDeatils, e, res.data.data)
							this.show = false
							console.log(this.dataDeatils)
							console.log(this.dataDeatils[e])
						}).catch(err => {
							console.log(err)
						})
					}
					
				} else {
					console.log("复习入口")
					// 复习
					if (this.currents == 0) {
						//错题
						siteBrushOneMenuWrong({
							token: this.token,
							id: this.dataList[e].id
						}).then(res => {
							console.log(res)
							this.$set(this.dataDeatils, e, res.data.data)
							this.show = false
							console.log(this.dataDeatils)
							console.log(this.dataDeatils[e])
						}).catch(err => {
							console.log(err)
						})
					} else {
						//收藏
						siteBrushOneMenuCollection({
							token: this.token,
							id: this.dataList[e].id
						}).then(res => {
							console.log(res)
							this.$set(this.dataDeatils, e, res.data.data)
							this.show = false
							console.log(this.dataDeatils)
							console.log(this.dataDeatils[e])
						}).catch(err => {
							console.log(err)
						})
					}
				}

			},
			close(e) {
				// console.log('close', e)
			},
			change(e) {
				console.log('change', e)
			},
			oneTab(item, index) {
				this.current = index
				uni.setStorage({
					key: 'last_time',
					data: index,
					success: function () {
						console.log('success');
					}
				});
				this.show = true
				if (this.enter == 0) {
					// 首页
					siteBrushTwoMenu({
						token: this.token,
						id: item.id
					}).then(res => {
						console.log(res)
						this.dataList = res.data.data
						this.show = false
						for (let i in res.data.data) {
							this.dataDeatils[i] = new Array()
						}
					}).catch(err => {
						console.log(err)
					})
				} else {
					console.log("复习入口")
					// 复习
					if (this.currents == 0) {
						//错题
						siteBrushTwoMenuWrong({
							token: this.token,
							id: item.id
						}).then(res => {
							console.log(res)
							this.dataList = res.data.data
							this.show = false
							for (let i in res.data.data) {
								this.dataDeatils[i] = new Array()
							}
						}).catch(err => {
							console.log(err)
						})
					} else {
						//收藏
						siteBrushTwoMenuCollection({
							token: this.token,
							id: item.id
						}).then(res => {
							console.log(res)
							this.dataList = res.data.data
							this.show = false
							for (let i in res.data.data) {
								this.dataDeatils[i] = new Array()
							}
						}).catch(err => {
							console.log(err)
						})
					}
				}
			},
			toDetails(e) {
				question({
					token: this.token,
					unit_id: e.id
				}).then(res => {
					console.log(res)
					if (res.data.code == -1 && res.data.data == '没有权限') {
						this.showAudition = true
					} else {
						uni.navigateTo({
							url: '/pages/index/Details?unit_it=' + e.id + '&wrong=' + this.wrong +
								"&title=" + e.title + "&sign=" + e.attr + "&answerCard=1" + "&isTrue=2"
						})
					}
				}).catch(err => {
					console.log(err)
				})
			},
			// 关闭弹窗
			closeAudition() {
				this.showAudition = false
			},
			// 发送邀请码
			sendAuditionCode() {
				invitation({
					token: this.token,
					audition_code: this.auditionCode
				}).then(res => {
					console.log(res)
					if (res.data.code == -1) {
						this.$u.toast(res.data.data)
					} else {
						this.$u.toast(res.data.data)
						this.showAudition = false
					}
				}).catch(err => {
					console.log(err)
				})
			}
		}
	}
</script>

<style lang="scss">
	.loadModul{
		position: fixed;
		z-index: 999;
		background-color: rgba(192, 192, 192, .8);
		border-radius: 20rpx;
		width: 300rpx;
		height: 250rpx;
		left: 50%;
		right: 50%;
		top:50%;
		bottom: 50%;
		transform: translate(-50%,-50%);
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.model {
		position: fixed;
		left: 0;
		top: 0;
		width: 100vw;
		height: 100vh;
		background-color: rgba(0, 0, 0, 0.4);
		z-index: 5;

		.models {
			width: 90%;
			left: 50%;
			right: 50%;
			position: fixed;
			margin-top: 65%;
			// border-radius: 20rpx;
			// padding: 10rpx 0;
			// background-color: #fff;
			transform: translate(-50%, -50%);
			text-align: center;

			img {
				width: 101%;
				height: 200rpx;
			}

			.contents {
				border-bottom-left-radius: 20rpx;
				border-bottom-right-radius: 20rpx;
				padding: 10rpx 0;
				margin: 0 30rpx;
				margin-top: -50rpx;
				background-color: #fff;
			}

			.closeModel {
				top: -20rpx;
				right: -10rpx;
				width: 50rpx;
				height: 50rpx;
				line-height: 50rpx;
				color: #fff;
				border: 1px solid #fff;
				font-size: 38rpx;
				text-align: center;
				position: absolute;
				border-radius: 50%;
			}

			.modelTitle {
				font-weight: 600;
				margin: 30rpx 0;
			}

			input {
				border: 1rpx solid #d1d1d1;
				width: 90%;
				margin: 0 5%;
				line-height: 88rpx;
				height: 88rpx;
				border-radius: 10rpx;
			}

			.buttons {
				display: flex;
				justify-content: space-between;
				padding: 50rpx 20rpx;

				button {
					color: #fff;
					border-radius: 10rpx;
					width: 45%;
					line-height: 80rpx;
				}

				.sure {
					background-color: #e73928;
				}

				.contact {
					background-color: #c0c0c0;
				}
			}
		}
	}

	.list {
		background-color: #f7f7fb;
		min-height: calc(100vh - 20vh);
		padding: 20rpx;

		.oneList {
			padding: 20rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;

			.oneLeft {
				display: flex;
				align-items: center;
				font-size: 32rpx;
				font-weight: 500;

				.title {
					width: 400rpx;
					overflow-x: scroll;
					white-space: nowrap;
					margin-right: 30rpx;
				}

				.icon {
					color: #fff;
					background-color: #6ab4ff;
					font-size: 20rpx;
					border-radius: 6rpx;
					padding: 2rpx 10rpx;
				}
			}

			.oneRight {
				display: flex;
				align-items: center;
				font-size: 24rpx;
				color: #c0c0c0;

				.number {
					display: flex;
					margin-right: 20rpx;

					.text {
						color: #6ab4ff;
					}
				}

				.arrow {
					width: 26rpx;
					height: 26rpx;
					transform: rotate(90deg);
				}

				.arrow_checked {
					transform: rotate(-90deg);
					transition: ease 0.4s;
				}
			}
		}

		.twoList {
			padding: 20rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;

			.twoListLeft {
				display: flex;
				align-items: center;
				color: #c0c0c0;
				font-size: 26rpx;

				.point {
					width: 20rpx;
					height: 20rpx;
					border-radius: 50%;
					background-color: #c0c0c0;
					margin-right: 20rpx;
				}

				.point_blue {
					background-color: #6ab4ff;
				}

				.title {
					width: 400rpx;
					overflow-x: scroll;
					white-space: nowrap;
					margin-right: 20rpx;
				}

				.icon {
					color: #fff;
					font-size: 20rpx;
					padding: 2rpx 10rpx;
					background-color: #6ab4ff;
					border-radius: 6rpx;
				}
			}

			.twoListRight {
				display: flex;
				align-items: center;
				font-size: 24rpx;
				color: #c0c0c0;

				.number {
					display: flex;
					margin-right: 20rpx;

					.text {
						color: #6ab4ff;
					}
				}
			}
		}

		.display_none {
			display: none;
			// transition: linear 0.1s;
		}

		.cell {
			background-color: #fff;
			margin-bottom: 2rpx;
		}

		.cell:first-child {
			border-top-left-radius: 20rpx;
			border-top-right-radius: 20rpx;
		}

		.cell:last-child {
			margin-bottom: 0;
		}
	}

	.head {
		padding: 50rpx 20rpx;
		display: flex;
		background-color: #dbeafc;

		.list_cell {
			margin-right: 50rpx;

			.purple {
				background-color: #c2bcff;
				color: #c2bcff;
			}

			.orange {
				background-color: #ffa885;
				color: #ffa885;
			}

			.blue {
				background-color: #9ac0ff;
				color: #9ac0ff;
			}

			.green {
				background-color: #7fe3b2;
				color: #7fe3b2;
			}

			.cells {
				width: 100rpx;
				height: 100rpx;
				border-radius: 10rpx;
				text-align: center;
				padding: 20rpx 10rpx;

				.title {
					background: rgba(255, 255, 255, 0.8);
					border-radius: 6rpx;
					font-weight: 600;
					line-height: 58rpx;
				}

				.num {
					color: #fff;
					line-height: 58rpx;
					font-size: 24rpx;
					text-shadow: 0px 1px 2px #000;
				}
			}

			.icon {
				width: 90rpx;
				height: 6rpx;
				margin: 0 auto;
				margin-top: 20rpx;
				background-color: #6ab4ff;
				border-radius: 20rpx;
			}
		}
	}

	.bodyNight {
		.head {
			background-color: #445362;
		}

		.list {
			background-color: #3b3b3b;
			height: 80vh;
			padding: 20rpx 30rpx;

			.cell {
				background-color: #303030;
				color: #fff;
			}
		}
	}
</style>
